import { Component, ElementRef, EventEmitter, Input, NgZone, OnChanges, Output, TemplateRef, ViewChild } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { WizardService } from 'src/app/wizard/wizard.service';
import { IBSSFileConfig } from '../../file-upload/file-upload.component';
import * as _ from "lodash";
import { MapsAPILoader } from '@agm/core';

import { MouseEvent as AGMMouseEvent } from '@agm/core';

@Component({
  selector: 'ibss-editabout2',
  templateUrl: './editabout2.component.html',
  styleUrls: ['./editabout2.component.scss']
})
export class Editabout2Component implements OnChanges {

  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputParam: any;
  @Input() index: any;
  @Input() isView: boolean = false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  latitude: number;
  longitude: number;
  zoom: number = 18;
  address: string;
  geoCoder: any;
  activeEvt: any = -1;
  @ViewChild('search') public searchElementRef: ElementRef;

  config: typeof CONFIG = CONFIG;
  editItem: any = null;
  activeColor: any = null;
  modalRef: BsModalRef;
  imgIndx: any = -1;
  @ViewChild('cropImage') cropImage!: TemplateRef<any>;

  imageFile: any = '';
  croppedImage: any = '';

  fileConfig: IBSSFileConfig = {
    sectionName: "about",
    imageName: "about2",
    isUnique: true,
    cropper: {
      aspectRatio: 16 / 9,
      maintainAspectRatio: true
    }
  }

  constructor(
    private ws: WizardService,
    private toastr: ToastrService,
    private modalService: BsModalService,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
  ) { }

  ngOnChanges(): void {
    console.log("EditIntro2Component", this.inputParam);
    this.editItem = JSON.parse(JSON.stringify(this.inputParam));

    this.setEventData()


  }

  ngAfterViewInit() {
    this.initPlaces();
  }

  setEventData() {
    this.editItem.events.map((event: any) => {
      event.date = new Date(event.date);
      event.time = new Date(event.time);
      if (_.isEmpty(event.showMap)) {
        event.showMap = true;
      }
      return event;
    })
  }

  updateEditor(close: any) {
    this.inputParam = JSON.parse(JSON.stringify(this.editItem));
    const params: any = {
      component: JSON.parse(JSON.stringify(this.inputParam)),
      action: "saveEdit",
      index: this.index,
      close: close
    };
    this.onAction.emit(params)
  }

  onSelectFile(file: any, index: any) {
    console.log("onSelectFile", file);

    if (index !== "backgroundImage") {
      if (this.editItem.events[index].image && this.editItem.events[index].image.indexOf(this.config.siteURL) != -1) {

        const imgName = JSON.parse(JSON.stringify(this.editItem.events[index].image)).split("/")
        const iName = imgName.pop();
        const payload = {
          "sectionName": "about",
          "fileNames": [iName]
        }
        this.ws.deleteFile(payload)
          .subscribe((res) => {
            console.log('deleteFile-success', res);
          }, (err) => {
            console.log('deleteFile-failure', err);
          })
      }
      this.editItem.events[index].image = JSON.parse(JSON.stringify(file));
    } else {
      if (this.editItem[index] && this.editItem[index].indexOf(this.config.siteURL) != -1) {

        const imgName = JSON.parse(JSON.stringify(this.editItem[index])).split("/")
        const iName = imgName.pop();
        const payload = {
          "sectionName": "about",
          "fileNames": [iName]
        }
        this.ws.deleteFile(payload)
          .subscribe((res) => {
            console.log('deleteFile-success', res);
          }, (err) => {
            console.log('deleteFile-failure', err);
          })
      }
      this.editItem[index] = JSON.parse(JSON.stringify(file));
    }
  }

  onDeleteImage(key: any) {
    let editItem = JSON.parse(JSON.stringify(this.editItem));

    if (editItem[key] && editItem[key].indexOf(this.config.siteURL) != -1) {

      const imgName = JSON.parse(JSON.stringify(editItem[key])).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName": "about",
        "fileNames": [iName]
      }
      this.ws.deleteFile(payload)
        .subscribe((res) => {
          console.log('deleteFile-success', res);
        }, (err) => {
          console.log('deleteFile-failure', err);
        })
    }
    editItem[key] = null;
    this.editItem = JSON.parse(JSON.stringify(editItem));
  }

  selectColor(key: any) {
    this.activeColor = {
      key: key,
      value: JSON.parse(JSON.stringify(this.editItem.colors[key]))
    }
  }

  changeComplete(color: any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.editItem.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.updateEditor(false);
  }

  onSaveColor() {
    this.activeColor = null;
  }

  onValueChange(event: any) {
    console.log(event);
    console.log(this.editItem);
  }

  onAddEvent() {
    this.editItem.events.push({
      name: "Wedding Party",
      image: "../../../../../assets/images/web/layout2/wedding-party.jpg",
      location: "Church End, Hendon, London, UK",
      date: new Date(),
      time: new Date(),
      description: "Event Description",
      showMap: true,
      mapConfig: {
        lat: this.latitude,
        lng: this.longitude,
        label: 'A',
        draggable: true,
        zoom: this.zoom,
        address: this.address
      }
    });

    this.activeEvt = this.editItem.length - 1;
  }

  onDeleteEvent(index: any) {
    this.activeEvt = this.activeEvt == index ? -1 : this.activeEvt;
    this.editItem.events.splice(index, 1);
    this.updateEditor(false);
  }

  onCropImage(index: any) {
    this.imgIndx = index;
    this.modalRef = this.modalService.show(this.cropImage, { class: 'modal-lg' });
  }

  saveCropped(): void {
    this.editItem.events[this.imgIndx].image = JSON.parse(JSON.stringify(this.croppedImage));
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
    this.modalRef.hide();
  }


  cancelCrop(): void {
    this.modalRef.hide();
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
  }

  onInvalidImage() {
    this.modalRef.hide();
    this.toastr.error("Invalid file format, only png, gif and jpg are allowed");
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }

  initPlaces() {
    this.mapsAPILoader.load().then(() => {
      if (this.activeEvt == -1) {
        this.setCurrentLocation();
        this.geoCoder = new google.maps.Geocoder;
      }

      if (this.activeEvt > -1) {
        this.geoCoder = new google.maps.Geocoder;
        let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement);
        autocomplete.addListener("place_changed", () => {
          this.ngZone.run(() => {
            let place: any = autocomplete.getPlace();
  
            if (place.geometry === undefined || place.geometry === null) {
              return;
            }
  
            this.latitude = place.geometry.location.lat();
            this.longitude = place.geometry.location.lng();
  
            if (this.activeEvt != -1) {
               if (_.isEmpty(this.editItem.events[this.activeEvt].mapConfig)) {
                this.editItem.events[this.activeEvt].mapConfig = {
                    lat: this.latitude,
                    lng: this.longitude,
                    label: '',
                    draggable: true,
                    zoom: this.zoom,
                    address: this.address,
                  };
                }
              this.editItem.events[this.activeEvt].mapConfig.lat = place.geometry.location.lat();
              this.editItem.events[this.activeEvt].mapConfig.lng = place.geometry.location.lng();
              this.editItem.events[this.activeEvt].mapConfig.label = place.name;
              this.editItem.events[this.activeEvt].mapConfig.draggable = true;
              this.editItem.events[this.activeEvt].mapConfig.zoom = this.zoom;
              this.editItem.events[this.activeEvt].mapConfig.address = place.adr_address;
  
              // this.editItem = JSON.parse(JSON.stringify(this.editItem));
            }
          });
        });
      }
    });
  }

  onPickLocation(indx: any) {
    this.activeEvt = indx;

    setTimeout(() => {
      this.initPlaces();
    }, 500)
  }

  private setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.getAddress(this.latitude, this.longitude);
      });
    }
  }

  getAddress(latitude: any, longitude: any, index: any = -1) {
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results: any, status: any) => {
      if (status === 'OK') {
        if (results[0]) {
          console.log("getAddress", results);
          this.address = results[0].formatted_address;

          if (index != -1) {
            this.editItem.events[this.activeEvt].mapConfig.address = results[0].formatted_address;
          }
        } else {
          window.alert('No results found');
        }
      } else {
        window.alert('Geocoder failed due to: ' + status);
      }
    });
  }

  
  onLocationPicked($event: AGMMouseEvent) {
    console.log('onLocationPicked', $event);
    if (this.activeEvt != -1) {
      this.editItem.events[this.activeEvt].mapConfig = {
        lat: $event.coords.lat,
        lng: $event.coords.lng,
        label: '',
        draggable: true,
        zoom: this.zoom,
        address: this.address,
      };

      this.getAddress($event.coords.lat, $event.coords.lng, this.activeEvt )
    }
  }
}
