import { Component, EventEmitter, Input, OnChanges, Output, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { IBSSFileConfig } from '../edit/file-upload/file-upload.component';
import { WIZARD_CONFIG } from '../wizard.config';
import { WizardService } from '../wizard.service';

@Component({
  selector: 'ibss-seo-config',
  templateUrl: './seo-config.component.html',
  styleUrls: ['./seo-config.component.scss']
})
export class SEOConfigComponent implements OnChanges {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @ViewChild('s1f') s1f: any;
  @Input() inputParam: any;
  @Input() isNew: any = true;
  @Output() onAction = new EventEmitter();
  allTypes = this.wiz_config.types;
  seoForm: any;
  bgFileConfig: IBSSFileConfig = {
    sectionName: "seo",
    imageName: "seo1",
    isUnique: true,
    customClass: "seo-comp",
    cropper: {
      aspectRatio: 4 / 3,
      maintainAspectRatio: true
    }
  }
  constructor(
    private ws: WizardService,
    private toastr: ToastrService
  ) {
    this.seoForm = new FormGroup({
      metaDataDescription: new FormControl("", [Validators.required]),
      keyword: new FormControl(""),
      keywords: new FormControl([]),
      metaDataKeyWords: new FormControl("", [Validators.required]),
      ogImageUrl: new FormControl(null, [Validators.required]),
      ogTitle: new FormControl("", [Validators.required, Validators.pattern('[a-zA-Z0-9\-_]{2,}$')]),
      ogType: new FormControl("website", [Validators.required])
    })
  }

  ngOnChanges() {
    console.log("isNew", !this.isNew);
    if (!_.isEmpty(_.get(this.inputParam, "seo"))) {
      this.seoForm.get('metaDataDescription').patchValue(this.inputParam.seo.metaDataDescription);
      this.seoForm.get('metaDataKeyWords').patchValue(this.inputParam.seo.metaDataKeyWords);
      const keywords = this.seoForm.get('metaDataKeyWords').value;
      this.seoForm.get('keywords').patchValue(keywords.split(","));
      this.seoForm.get('ogImageUrl').patchValue(this.inputParam.seo.ogImageUrl);
      this.seoForm.get('ogTitle').patchValue(this.inputParam.seo.ogTitle);
      this.seoForm.get('ogType').patchValue(this.inputParam.seo.ogType);
    }
    console.log(this.seoForm);
  }

  addKeywords() {
    const keyWord = this.seoForm.get('keyword').value;
    if (!_.isEmpty(keyWord)) {
      const allKeywords = this.seoForm.get('keywords').value;

      if (allKeywords.indexOf(keyWord) == -1) {
        allKeywords.push(keyWord);
        this.seoForm.get('keywords').patchValue(allKeywords);
        this.seoForm.get('metaDataKeyWords').patchValue(allKeywords.join());
        this.seoForm.get('keyword').patchValue("");
      } else {
        this.toastr.error("Keyword already exist, please enter a different keyword.");
      }
    } else {
      this.toastr.error("Please enter a valid keyword.");
    }
  }

  removekeyword(index: any) {
    const allKeywords = this.seoForm.get('keywords').value;
    allKeywords.splice(index, 1);
    this.seoForm.get('keywords').patchValue(allKeywords);
    this.seoForm.get('metaDataKeyWords').patchValue(allKeywords.join());
  }

  goToNext() {

    const seo: any = {
      metaDataDescription: this.seoForm.get('metaDataDescription').value,
      metaDataKeyWords: this.seoForm.get('metaDataKeyWords').value,
      ogImageUrl: this.seoForm.get('ogImageUrl').value,
      ogTitle: this.seoForm.get('ogTitle').value,
      ogType: this.seoForm.get('ogType').value
    }
    // this.inputParam.currentStep = 1;
    this.inputParam.seo = seo;
    const params: any = {
      inputParam: this.inputParam,
      seo: seo,
      action: "MODIFY"
    };
    this.onAction.emit(params)
  }

  onDeleteImage(key: any) {
    let editItem = JSON.parse(JSON.stringify(this.seoForm.get(key).value));

    if (editItem && editItem.indexOf(this.config.siteURL) != -1) {

      const imgName = JSON.parse(JSON.stringify(editItem)).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName": "seo",
        "fileNames": [iName]
      }
      this.ws.deleteFile(payload)
        .subscribe((data) => {
          console.log('deleteFile-success', data);
        }, (err) => {
          console.log('deleteFile-failure', err);
        })
    }
    this.seoForm.get(key).patchValue(null);
  }

  onSelectFile(res: any, key: any) {
    console.log("onSelectFile", res);
    let editItem = JSON.parse(JSON.stringify(this.seoForm.get(key).value));

    if (editItem && editItem.indexOf(this.config.siteURL) != -1) {

      const imgName = JSON.parse(JSON.stringify(editItem)).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName": "seo",
        "fileNames": [iName]
      }
      this.ws.deleteFile(payload)
        .subscribe((data) => {
          console.log('deleteFile-success', data);
        }, (err) => {
          console.log('deleteFile-failure', err);
        })
    }
    this.seoForm.get(key).patchValue(res);
  }

}
