import { Component, EventEmitter, Input, OnChanges, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { CONFIG } from 'src/app/config';
import { WIZARD_CONFIG } from '../wizard.config';
import * as  _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'ibss-site-config',
  templateUrl: './site-config.component.html',
  styleUrls: ['./site-config.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SiteConfigComponent implements OnInit, OnChanges {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @Input() inputParam: any;
  @Output() onAction = new EventEmitter();
  allLayouts = this.wiz_config.web_layouts;
  selectedHeader: any;
  payload: any;
  currentTab = 1;
  subtab = 1;
  isTemplateSelected = false;
  constructor(
    private toastr: ToastrService
  ) { }

  ngOnInit(): void {
  }

  ngOnChanges() {
    console.log('SiteConfigComponent', this.inputParam);

    this.isTemplateSelected = !_.isEmpty(this.inputParam.templateID);
    console.log('this.isTemplateSelected', this.isTemplateSelected);
  }

  goToNext(event: any) {
    const seo = _.isEmpty(this.inputParam.seo) || _.isEmpty(this.inputParam.seo.metaDataDescription)
    || _.isEmpty(this.inputParam.seo.metaDataKeyWords) || _.isEmpty(this.inputParam.seo.ogImageUrl)
    || _.isEmpty(this.inputParam.seo.ogTitle) || _.isEmpty(this.inputParam.seo.ogType);

    if (_.isEmpty(this.inputParam.templateID)) {
      this.toastr.error("Please Choose a template to proceed");
    } 
    // else if (seo){
    //   this.toastr.error("Please Configure SEO contents.");
    // } 
    else {
      this.inputParam.currentStep = 2;
      // const params: any = {
      //   inputParam:this.inputParam,
      //   action:"localSave",
      //   index:event
      // };
      this.onAction.emit(event);
    }
  }

  onChangeTab(id: any) {
    const seo = _.isEmpty(this.inputParam.seo) || _.isEmpty(this.inputParam.seo.metaDataDescription)
    || _.isEmpty(this.inputParam.seo.metaDataKeyWords) || _.isEmpty(this.inputParam.seo.ogImageUrl)
    || _.isEmpty(this.inputParam.seo.ogTitle) || _.isEmpty(this.inputParam.seo.ogType);

    if (_.isEmpty(this.inputParam.templateID)) {
      this.toastr.error("Please Choose a Template to proceed");
    }
    // else if (seo){
    //   this.toastr.error("Please Configure SEO contents.");
    // } 
    else {
      this.currentTab = id;
    }
  }
}
