import { Component, EventEmitter, Input, NgZone, OnChanges, Output } from '@angular/core';
import { CONFIG } from 'src/app/config';
import { IBSSFileConfig } from '../edit/file-upload/file-upload.component';
import { WIZARD_CONFIG } from '../wizard.config';
import { WizardService } from '../wizard.service';

@Component({
  selector: 'ibss-choose-footer',
  templateUrl: './choose-footer.component.html',
  styleUrls: ['./choose-footer.component.scss']
})
export class ChooseFooterComponent implements OnChanges {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @Input() inputParam: any;
  @Output() onAction = new EventEmitter();
  selectedFooter:any;
  selectedFooterTemplate:any ={};
  payload: any;
  subtab=1;
  footerTemplates = this.wiz_config.footer_layouts;
  currentPage=1;
  selectedIndex=-1;
  activeColor:any =null;

  bgFileConfig: IBSSFileConfig = {
    sectionName:"header",
    imageName:"header1",
    isUnique: true,
    customClass: "header-comp",
    cropper : {
      aspectRatio: 8/1,
      maintainAspectRatio:true
    }
  }

  constructor(
    public ngZone:NgZone,
    private ws: WizardService,
  ) { }

  ngOnChanges() {
    console.log('ChoosefooterComponent', this.inputParam);
    console.log('footerTemplates', this.footerTemplates);
    this.setselectedFooter();
  }

  setselectedFooter() {
    if (this.inputParam) {
      let footer= this.inputParam.pages.filter((page:any) => page.id === 'footerComponent');

      if (footer && footer.length > 0) {
        this.selectedFooter = footer[0];
        this.selectedFooterTemplate = footer[0].template;
      }
    }
  }

  goToNext(event:any) {
    this.inputParam.currentStep = 2;
    this.inputParam.currentStep = 2;
    const params: any = {
      inputParam:this.inputParam,
      action:"localSave"
    };
    this.onAction.emit(params)
  }

  onToggleSubTab(id:any) {
    this.subtab = id;
  }


  public fileOver(event: any){
    console.log(event);
  }

  public fileLeave(event: any){
    console.log(event);
  }

  onSelectFile(res:any, key:any) {
    console.log("onSelectFile", res);
    let editItem = JSON.parse(JSON.stringify(this.selectedFooterTemplate));

    if (editItem[key] && editItem[key].indexOf(this.config.siteURL)!=-1) {

      const imgName=JSON.parse(JSON.stringify(editItem[key])).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "header",
        "fileNames" : [iName]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }
    editItem[key] = res;
    this.selectedFooterTemplate = JSON.parse(JSON.stringify(editItem));
  }

  onDeleteImage ( key:any) {
    let editItem = JSON.parse(JSON.stringify(this.selectedFooterTemplate));

    if (editItem[key] && editItem[key].indexOf(this.config.siteURL)!=-1) {

      const imgName=JSON.parse(JSON.stringify(editItem[key])).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "header",
        "fileNames" : [iName]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }
    editItem[key] = null;
    this.selectedFooterTemplate = JSON.parse(JSON.stringify(editItem));
  }

  onSelectFooter(template:any) {
    const existingTemplate = JSON.parse(JSON.stringify(this.inputParam.pages[this.inputParam.pages.length-1].template));
    for (let x in template.content) {
      if (x !== "id" && x !== "colors" && template.content[x] ) {
        template.content[x] = existingTemplate[x] ?  existingTemplate[x] : template.content[x];
      }
    }
    this.inputParam.pages[this.inputParam.pages.length-1].template = JSON.parse(JSON.stringify(template.content));
    // this.inputParam.fonts = JSON.parse(JSON.stringify(template.content.fonts));
    // this.inputParam.pages = JSON.parse(JSON.stringify(template.content.pages));
    const params: any = {
      inputParam:this.inputParam,
      action:"localSave",
      index:this.inputParam.pages.length-1
    };
    this.onAction.emit(params)
  }


  onSaveChanges() {
    let hIndx = -1;
    let footer = JSON.parse(JSON.stringify(this.selectedFooter));
    const template = JSON.parse(JSON.stringify(this.selectedFooterTemplate));

    this.selectedIndex = this.inputParam.pages.length-1;
    const lastSection = JSON.parse(JSON.stringify(this.inputParam.pages[this.inputParam.pages.length-1]));
    if (lastSection.id === "footerComponent") {
      this.inputParam.pages[this.selectedIndex].template = template;
      this.inputParam.pages[this.selectedIndex].menuId = "footerComponent_1";
      this.inputParam.pages[this.selectedIndex].menuTitle = "Footer";

      this.inputParam.pages[0].template.pages.forEach((page:any, index:number)=> {
        if (page.id === footer.id) {
          hIndx = index;
        }
      });
    } else {
      const page = {
        id: "footerComponent",
        menuId: "footerComponent_1",
        menuParent: "",
        menuTitle: "Footer",
        menuType: "",
        template: template
     }
      this.inputParam.pages.push(page);
    }

    const params: any = {
      inputParam:this.inputParam,
      action:"localSave",
      index:this.inputParam.pages.length-1
    };
    this.onAction.emit(params)

    this.onValueChange();
  }

  selectColor(key:any) {
    this.activeColor = {
      key:key,
      value:JSON.parse(JSON.stringify(this.selectedFooterTemplate.colors[key]))
    }
  }

  changeComplete(color:any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.selectedFooterTemplate.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.onValueChange();
  }
  
  onSaveColor() {
    this.activeColor = null;
  }

  onValueChange(){
    this.selectedFooterTemplate = JSON.parse(JSON.stringify(this.selectedFooterTemplate));
  }
}
