import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { CONFIG } from 'src/app/config';
import { WIZARD_CONFIG } from '../wizard.config';

@Component({
  selector: 'ibss-choose-templates',
  templateUrl: './choose-templates.component.html',
  styleUrls: ['./choose-templates.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ChooseTemplatesComponent implements OnInit {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @Input() inputParam: any;
  @Output() onAction = new EventEmitter();
  templates: any;

  constructor() { }

  ngOnInit(): void {
    console.log("ChooseTemplatesComponent", this.inputParam);
    this.templates = this.wiz_config.web_templates;
  }

  onSelectLayout(template:any) {
    this.inputParam.templateID = template.id;
    this.inputParam.fonts = JSON.parse(JSON.stringify(template.content.fonts));
    this.inputParam.pages = JSON.parse(JSON.stringify(template.content.pages));
    const params: any = {
      inputParam:this.inputParam,
      action:"localSave"
    };
    this.onAction.emit(params)
  }
}
