import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { CONFIG } from 'src/app/config';
import { WIZARD_CONFIG } from '../wizard.config';

@Component({
  selector: 'ibss-edit-templates',
  templateUrl: './edit-templates.component.html',
  styleUrls: ['./edit-templates.component.scss']
})
export class EditTemplatesComponent implements OnInit {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @ViewChild('s4f') s4f: any;
  @Input() inputParam: any;
  @Output() onAction = new EventEmitter();
  allLayouts = this.wiz_config.web_layouts;
  viewWidth = "800px";
  url="";
  constructor() { }

  ngOnInit(): void {

  }

  public handleAction(evt: any) { 
    this.onAction.emit(evt)
  }

  onActionHandler(action:any) {
    const params: any = {
      inputParam:this.inputParam,
      action: action!=="PUBLISH" ? "lastStage": action,
      additonalAction:action!=="PUBLISH" ? action: ""
    };
    this.onAction.emit(params)
  }

}
