import { Component, EventEmitter, Input, NgZone, OnChanges, Output, TemplateRef, ViewChild } from '@angular/core';
import { CONFIG } from 'src/app/config';
import { WIZARD_CONFIG } from '../wizard.config';
import { BsModalService, BsModalRef, ModalDirective } from 'ngx-bootstrap/modal';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
import { Observable, of, Subscriber } from 'rxjs';
import { WizardService } from '../wizard.service';
import { mergeMap } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { IBSSFileConfig } from '../edit/file-upload/file-upload.component';
@Component({
  selector: 'ibss-choose-header',
  templateUrl: './choose-header.component.html',
  styleUrls: ['./choose-header.component.scss']
})
export class ChooseHeaderComponent implements OnChanges {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @Input() inputParam: any;
  @Output() onAction = new EventEmitter();
  selectedHeader:any;
  editItem:any;
  payload: any;
  subtab=1;
  headerTemplates = this.wiz_config.header_layouts;
  createItem:any;
  showAddForm =false;
  allComponents =  this.wiz_config.allComponents;
  modalRef: BsModalRef;
  filteredComponents:any = [];
  selectedComponent:any = null;
  selectedIndex:any = -1;
  canEditForm = false;
  isNewSection = false;
  currentPage=1;
  activeColor: any = null;
  allFontsList: any= [];
  filteredFonts: any= [];
  selectedFont:any = "";
  typeaheadLoading: boolean;

  imageFile: any = '';
  croppedImage: any = '';

  fileConfig: IBSSFileConfig = {
    sectionName:"header",
    imageName:"header1",
    isUnique: true,
    customClass: "header-comp",
    cropper : {
      aspectRatio: 1/1,
      maintainAspectRatio:true
    }
  }

  bgFileConfig: IBSSFileConfig = {
    sectionName:"header",
    imageName:"header1",
    isUnique: true,
    customClass: "header-comp",
    cropper : {
      aspectRatio: 8/1,
      maintainAspectRatio:true
    }
  }

  @ViewChild(ModalDirective) editHeaderModal: ModalDirective;

  modalConfig = {
    backdrop: true,
    ignoreBackdropClick: false,
    class: 'ibss-edit-components modal-lg'
  };

  @ViewChild('cropImage') cropImage!: TemplateRef<any>; 

  profileDetails:any;
  constructor(
    private modalService: BsModalService,
    private ws: WizardService,
    private toastr: ToastrService) { 
    this.filteredFonts = new Observable((observer: Subscriber<any>) => {
      // Runs on every search
      observer.next(this.selectedFont);
    })
      .pipe(
        mergeMap((token: string) => this.getStatesAsObservable(token))
      );
  }

  getStatesAsObservable(token: string): Observable<any[]> {
    const query = new RegExp(token, 'i');
 
    return of(
      this.allFontsList.items.filter((font: any) => {
        return query.test(font.family);
      })
    );
  }

  ngOnChanges() {
    console.log('ChooseHeaderComponent', this.inputParam);
    console.log('headerTemplates', this.headerTemplates);
    this.filteredComponents = this.allComponents.slice(0, 1);
    this.profileDetails = this.ws.getProfileData();
    this.setSelectedHeader();

/*     this.ws.getGoogleFonts()
    .subscribe((res:any)=> {
      console.log("getGoogleFonts", res);
      this.allFontsList = res;
    }) */
  }

  setSelectedHeader() {
    if (this.inputParam) {
      let header= this.inputParam.pages.filter((page:any) => page.id === 'headerComponent');

      if (header && header.length > 0) {
        this.selectedHeader = JSON.parse(JSON.stringify(header[0]));
        this.editItem = JSON.parse(JSON.stringify(header[0].template));
      }
    }
  }

  onSelectFile(res:any, key:any) {
    console.log("onSelectFile", res);
    let editItem = JSON.parse(JSON.stringify(this.editItem));

    if (editItem[key] && editItem[key].indexOf(this.config.siteURL)!=-1) {

      const imgName=JSON.parse(JSON.stringify(editItem[key])).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "header",
        "fileNames" : [iName],
        "filePath":editItem[key]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }
    editItem[key] = res;
    this.editItem = JSON.parse(JSON.stringify(editItem));
  }

  onDeleteImage ( key:any) {
    let editItem = JSON.parse(JSON.stringify(this.editItem));

    if (editItem[key] && editItem[key].indexOf(this.config.siteURL)!=-1) {
      console.log("Deleting", editItem[key]);
      const imgName=JSON.parse(JSON.stringify(editItem[key])).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "header",
        "fileNames" : [iName],
        "filePath":editItem[key]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }
    editItem[key] = null;
    this.editItem = JSON.parse(JSON.stringify(editItem));
  }

  onToggleSubTab(id:any) {
    this.subtab = id;
    // this.changeDetector.detectChanges();
  }
  
  onSelectHeader(template:any) {
    if (this.inputParam.pages[0].id === "headerComponent") {
      this.inputParam.pages[0].template.id = template.content.id;
      this.inputParam.pages[0].template.colors = template.content.colors;
    } else {

      const page = {
          id: template.id,
          menuId: template.id+"_1",
          menuParent: "",
          menuTitle: "header",
          menuType: "",
          template: template.content
      }
      this.inputParam.pages.unshift(page);
    }

    const params: any = {
      inputParam:JSON.parse(JSON.stringify(this.inputParam)),
      action:"localSave",
      index:0
    };
    this.onAction.emit(params)
  }

  listSorted(sortedList:any) {
    console.log(sortedList);

    this.onCancelChanges();

    this.editItem.pages=sortedList;
    let orderIds:any = [];
  
    sortedList.forEach((page:any) => {
      orderIds.push(page.id);
    });
    
    let compCount:any = {};
    this.inputParam.pages.map((page:any) => {
        let order= orderIds.indexOf(page.menuId);

        if (page.id === "headerComponent") {
          page.order = 0;
        } else if (page.id === "footerComponent") {
          page.order = this.inputParam.pages.length-1;
        }
        else if (order!=-1) {
          page.order = (order+1);
        }

        if (!compCount[page.id]) {
          compCount[page.id] = 1;
        } else {
          compCount[page.id]++;
        }

        page.menuId = page.id+"_"+compCount[page.id];

        return page;
    });
    this.inputParam.pages = this.inputParam.pages.sort((a:any, b:any) => (a.order > b.order) ? 1 : -1 );
    console.log("compCount", compCount)
    console.log("listSorted",this.inputParam);
    
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  pageChanged(event: PageChangedEvent): void {
    const startItem = (event.page - 1) * 1;
    const endItem = event.page * 1;
    this.filteredComponents = this.allComponents.slice(startItem, endItem);
  }

  onViewSection(section: any) {
    this.createItem = JSON.parse(JSON.stringify(section));
    this.createItem.section = this.createItem.id.split("_")[0];
    // this.selectedIndex = index;
    this.showAddForm = true;
    this.toggleEditModal();
    this.onSetSelectedComponent(section);
  }

  onSetSelectedComponent(section: any) {
    let pIndex =-1;
    let selectedPage:any = null;
    this.inputParam.pages.forEach((page:any, index:number)=> {
      if (page.menuId === section.id) {
        selectedPage = page;
        this.selectedIndex = index;
      }
    });

    if (selectedPage) {
      this.allComponents.forEach((page:any, index: number)=> {
        if (page.template.id === selectedPage.template.id) {
           pIndex=index;
        }
      });
    }
    if (pIndex!=-1) {
      this.currentPage = pIndex+1;
    }

    this.selectedComponent = selectedPage;
  }

  onAdd() {
    this.createItem = {
      id:"",
      title:"",
      subPages:[],
      type:"section"
    }
    this.showAddForm = true;
    this.canEditForm = true;
    this.currentPage = 1;
    this.toggleEditModal();
  }

  onEdit(page: any) {
    this.canEditForm = true;
  }

  onSelectComponent(component:any) {
    console.log('onSelectComponent',component);
    this.selectedComponent = JSON.parse(JSON.stringify(component));
  }

  onSaveChanges() {
      let hIndx = -1;
      this.selectedHeader.template=JSON.parse(JSON.stringify( this.editItem));
      let sHeader = this.selectedHeader && JSON.parse(JSON.stringify(this.selectedHeader));
      let header = this.createItem && JSON.parse(JSON.stringify(this.createItem));
      const component = this.selectedComponent && JSON.parse(JSON.stringify(this.selectedComponent));
      
      if (component) {
        header.id = this.formHeaderId();
      }
      
      console.log("sHeader", sHeader);
      console.log("header", header);
      console.log("component", component);

      
      if (this.showAddForm) {
        let canProceed = true;
        if (header.title == null || header.title === undefined || header.title.trim().length == 0 ) {
          this.toastr.error("Please Enter the title for the section");
          canProceed = false;
        } else  if (header.id == null || header.id === undefined || header.id.trim().length == 0 ) {
          this.toastr.error("Please Choose a component for the section");
          canProceed = false;
        }
        if (!canProceed) return;
      }
      
      const inputParam = JSON.parse(JSON.stringify(this.inputParam));
      
      if (inputParam.pages[0].id === "headerComponent") {
        inputParam.pages[0].template = JSON.parse(JSON.stringify(this.editItem));
      }

      if (this.selectedIndex !=-1) {
        
        // component.template.title=sHeader.template.title;
        // component.template.logo=sHeader.template.logo;
        inputParam.pages[this.selectedIndex].id = component.id;
        inputParam.pages[this.selectedIndex].template = component.template;
        inputParam.pages[this.selectedIndex].menuId = header.id;
        inputParam.pages[this.selectedIndex].menuTitle = header.title;
        
        inputParam.pages[0].template.pages[this.selectedIndex-1].id = header.id;
        inputParam.pages[0].template.pages[this.selectedIndex-1].subPages = header.subPages;
        inputParam.pages[0].template.pages[this.selectedIndex-1].title = header.title;
        inputParam.pages[0].template.pages[this.selectedIndex-1].type = header.type;

        // inputParam.pages[0].template.pages.forEach((page:any, index:number)=> {
        //   if (page.id === header.id) {
        //     hIndx = index;
        //   }
        // });
        

        // if (hIndx !==-1) {
        //   inputParam.pages[0].template.pages[hIndx].id = header.id;
        //   inputParam.pages[0].template.pages[hIndx].subPages = header.subPages;
        //   inputParam.pages[0].template.pages[hIndx].title = header.title;
        //   inputParam.pages[0].template.pages[hIndx].type = header.type;
        // }
      } else {
        
        if (component) {
          const page = {
            id: component.id,
            menuId: header.id,
            menuParent: "",
            menuTitle: header.title,
            menuType: header.type,
            template: component.template
          }
          inputParam.pages[0].template.pages.push(header);
          inputParam.pages.splice(inputParam.pages.length-1, 0, page);
        } else if (inputParam.pages[0].id !== "headerComponent") {
          const page = {
            id: "headerComponent",
            menuId: "headerComponent_1",
            menuParent: "",
            menuTitle: "",
            menuType: "",
            template: JSON.parse(JSON.stringify(this.editItem))
          }
          inputParam.pages.unshift(page);
        }
      }
  
      this.inputParam = JSON.parse(JSON.stringify(inputParam));
      this.selectedComponent = null;
      this.selectedIndex = -1;
      this.createItem = {
        id:"",
        title:"",
        subPages:[],
        type:"section"
      }
      this.showAddForm = false;
      this.canEditForm = false;
      this.toggleEditModal();
      this.isNewSection = false;
      this.setSelectedHeader();
      const params: any = {
        inputParam:JSON.parse(JSON.stringify(this.inputParam)),
        action:"localSave",
        index:0
      };
    this.onAction.emit(params);
  }

  formHeaderId() {
    let filteredPages: any= [];
    let itemNum = 1;

    if (this.selectedIndex !=-1 && this.createItem.id.includes(this.selectedComponent.id)) {
      return this.createItem.id;
    }

    this.inputParam.pages.forEach((page:any)=>{
        if (page.id === this.selectedComponent.id) {
          filteredPages.push(page.menuId);
        } 
    });
    filteredPages.sort();

    let lastItem = filteredPages.pop();

    if (lastItem) {
      lastItem = lastItem.split("_")
  
      if (lastItem.length>0) {
        itemNum = parseInt(lastItem[1]) +1;
      }
    }

    return this.selectedComponent.id+"_"+itemNum;
  }

  onDeleteSection(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
  }

  confirm(): void {
    let hIndx = -1;
    let header = this.createItem && JSON.parse(JSON.stringify(this.createItem));
    const inputParam = JSON.parse(JSON.stringify(this.inputParam));
    inputParam.pages[0].template.pages.forEach((page:any, index:number)=> {
      if (page.id === header.id) {
        hIndx = index;
      }
    });

    if (hIndx !==-1) {
      inputParam.pages[0].template.pages.splice(hIndx,1);
    }

    
    inputParam.pages.splice(this.selectedIndex,1);

    this.inputParam = JSON.parse(JSON.stringify(inputParam));
    this.selectedComponent = null;
    this.selectedIndex = -1;
    this.createItem = {
      id:"",
      title:"",
      subPages:[],
      type:"section"
    }
    this.showAddForm = false;
    this.canEditForm = false;
    this.toggleEditModal();
    this.isNewSection = false;
    const params: any = {
      inputParam:JSON.parse(JSON.stringify(this.inputParam)),
      action:"localSave",
      index:0
    };
    this.modalRef.hide();
    this.setSelectedHeader();
    this.onAction.emit(params)
  }
 
  decline(): void {
    this.modalRef.hide();
  }

  onCancelChanges() {
    this.selectedComponent = null;
    this.selectedIndex = -1;
    this.createItem = {
      id:"",
      title:"",
      subPages:[],
      type:"section"
    }
    this.showAddForm = false;
    this.canEditForm = false;
    this.toggleEditModal();
    this.isNewSection = false;
  }

  selectColor(key:any) {
    this.activeColor = {
      key:key,
      value:JSON.parse(JSON.stringify(this.editItem.colors[key]))
    }
  }

  changeComplete(color:any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.editItem.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.onValueChange();
  }
  
  onSaveColor() {
    this.activeColor = null;
  }

  onValueChange(){
    this.editItem = JSON.parse(JSON.stringify(this.editItem));
    // this.editItem = JSON.parse(JSON.stringify(editItem));
  }
  
  changeTypeaheadLoading(e: boolean): void {
    this.typeaheadLoading = e;
  }

  deleteImages(component:any) {
    console.log(component);
  }

  toggleEditModal() {
    if (this.showAddForm) {
      this.editHeaderModal.show();
    } else {
      this.editHeaderModal.hide();
    }
  }
}
