import { Component, EventEmitter, Input, OnChanges, Output, TemplateRef, ViewChild } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { WizardService } from 'src/app/wizard/wizard.service';
import { IBSSFileConfig } from '../../file-upload/file-upload.component';
import * as moment from 'moment-timezone'; 
import * as _ from 'lodash';
@Component({
  selector: 'ibss-edit-intro3',
  templateUrl: './edit-intro3.component.html',
  styleUrls: ['./edit-intro3.component.scss']
})
export class EditIntro3Component implements OnChanges {
  @Input() isDummy: boolean = true;
  @Input() isEdit: boolean = false;
  @Input() inputParam: any;
  @Input() index: any;
  @Input() isView: boolean= false;
  @Input() menuId: any;
  @Output() onNav = new EventEmitter();
  @Output() onAction = new EventEmitter();
  config:typeof CONFIG = CONFIG;
  editItem:any = null;
  activeColor:any =null;

  modalRef: BsModalRef;
  imgIndx: any = -1;
  @ViewChild('cropImage') cropImage!: TemplateRef<any>; 
  
  imageFile: any = '';
  croppedImage: any = '';

  profilConfig: IBSSFileConfig = {
    sectionName:"intro",
    imageName:"intro3",
    isUnique: true,
    cropper : {
      aspectRatio: 1/1,
      maintainAspectRatio:true
    }
  }

  bgFileConfig: IBSSFileConfig = {
    sectionName:"intro",
    imageName:"intro3Bg",
    isUnique: false,
    cropper : {
      aspectRatio: 16/9,
      maintainAspectRatio:true
    }
  }
  
  constructor(
    private ws:WizardService,
    private toastr: ToastrService,
    private modalService: BsModalService
  ) { }

  ngOnChanges(): void {
    console.log("EditIntro3Component", this.inputParam);
    this.editItem = JSON.parse(JSON.stringify(this.inputParam));
    this.editItem.dob = moment(this.editItem.dob).isValid() ? new Date(this.editItem.dob) : "";

    if(_.isEmpty(this.editItem.colors.cardBgColor)) {
      this.editItem.colors.cardBgColor ="#ffffff";
    }

    if(_.isEmpty(this.editItem.cardBgImage)) {
      this.editItem.cardBgImage = null;
    }

  }

  updateEditor(close:any) {
    this.inputParam = JSON.parse(JSON.stringify(this.editItem));
    const params: any = {
      component:JSON.parse(JSON.stringify(this.inputParam)),
      action:"saveEdit",
      index:this.index,
      close:close
    };
    this.onAction.emit(params)
  }

  onSelectFile(image:any, key:any) {
    const editItem = JSON.parse(JSON.stringify(this.editItem));
    const img = ( key==="backgroundImage" || key === "cardBgImage") ? editItem[key]:editItem[key].image;
    if (img && img.indexOf(this.config.siteURL)!=-1) {
  
      const imgName=JSON.parse(JSON.stringify(img)).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "intro",
        "fileNames" : [iName]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }

    if(key==="backgroundImage"  || key === "cardBgImage")  {
      editItem[key] = image;
    } else {
      editItem[key].image = image;
    }

    this.editItem = JSON.parse(JSON.stringify(editItem));
  }

  onDeleteImage (key:any) {
    let editItem = JSON.parse(JSON.stringify(this.editItem));
    const img = ( key==="backgroundImage" || key === "cardBgImage") ? editItem[key]:editItem[key].image;

    if (img && img.indexOf(this.config.siteURL)!=-1) {

      const imgName=JSON.parse(JSON.stringify(img)).split("/")
      const iName = imgName.pop();
      const payload = {
        "sectionName" : "intro",
        "fileNames" : [iName]
      }
      this.ws.deleteFile(payload)
      .subscribe((res)=> {
        console.log('deleteFile-success',res);
      },(err)=> {
        console.log('deleteFile-failure',err);
      })
    }
    if(key==="backgroundImage"  || key === "cardBgImage")  {
      editItem[key] = null;
    } else {
      editItem[key].image = null;
    }
    this.editItem = JSON.parse(JSON.stringify(editItem));
  }

  selectColor(key:any) {
    this.activeColor = {
      key:key,
      value:JSON.parse(JSON.stringify(this.editItem.colors[key]))
    }
  }

  changeComplete(color:any) {
    console.log("changeComplete", color);
    this.activeColor.value = color.color.hex;
    this.editItem.colors[this.activeColor.key] = JSON.parse(JSON.stringify(this.activeColor.value));
    this.updateEditor(false);
  }
  
  onSaveColor() {
    this.activeColor = null;
  }

  onCropImage(index:any) {
    this.imgIndx = index;
    this.modalRef = this.modalService.show(this.cropImage, {class: 'modal-lg'});
  }

  saveCropped(): void {
    this.editItem[this.imgIndx].image = JSON.parse(JSON.stringify(this.croppedImage));
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
    this.modalRef.hide();
  }
 

  cancelCrop(): void {
    this.modalRef.hide();
    this.imageFile = null;
    this.imgIndx = -1;
    this.croppedImage = null;
  }

  onInvalidImage() {
    this.modalRef.hide();
    this.toastr.error("Invalid file format, only png, gif and jpg are allowed");
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }
}
